<template>
  <div class="zc">
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" />
    <van-cell-group>
      <van-field v-model="username" required clearable label="" placeholder="" />
      <van-field v-model="password" type="password" label="" placeholder="" required />
      <van-field v-model="password1" type="password" label="" placeholder="" required />
    </van-cell-group>
    <van-button type="primary" size="large" @click="getdata"></van-button>
  </div>
</template>


<script>
import { Notify } from "vant";

export default {
  data() {
    return {
      username: null,
      password: null,
      password1: null
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    //ajax
    getdata() {
      if (this.password == this.password1) {
        this.$axios
          .get("/register", {
            params: {
              //
              username: this.username,
              password: this.password
            }
          })
          .then(data => {
            //
            window.console.log(data);
            if (data.data.code == 200) {
              // 
              Notify({
                message: "",
                type: "success",
                duration: 2000,
                onOpened: () => {
                  this.$router.push("/dl");
                }
              });
            }
          });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.zc {
  margin: -43px 0 -55px 0;
}
</style>